@import url('https://fonts.googleapis.com/css?family=Lato&display=swap');

:root {
  --box-shadow: 0 -2px 10px rgba(0, 0, 0, 1);
}

* {
  box-sizing: border-box;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

body {
  background-color: #121212;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: 'Lato', sans-serif;
}

.container {
  margin: 0px auto;
  width: 325px;
}

.header {
  text-align: right;
  margin-top: 10px;
}

/* .sleep-goal {
  display: flex;
  flex-direction: column;
  align-items: center;
} */

.hint {
  color: #454446;
  font-size: 14px;
  text-transform: lowercase;
}

.timeRange {
  color: #454446;
  font-size: 0.8em;
}

h1 {
  letter-spacing: 1px;
  margin: 0;
  display: inline;
}

h3 {
  border-bottom: 1px solid #1f1b24;
  padding-bottom: 10px;
  margin: 40px 0 10px;
}

h4 {
  margin: 0;
  text-transform: uppercase;
}

.inc-exp-container {
  background-color: #1f1b24;
  border-radius: 3px;
  box-shadow: var(--box-shadow);
  padding: 20px;
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
}

.inc-exp-container > div {
  flex: 1;
  text-align: center;
}

.inc-exp-container > div:first-of-type {
  border-right: 1px solid #1c191f;
}

.stats {
  font-size: 20px;
  letter-spacing: 1px;
  margin: 5px 0;
}

.stats.plus {
  color: #2ecc71;
}

.stats.minus {
  color: #c0392b;
}

label {
  display: inline-block;
  margin: 10px 0;
}

.inputHoursGoal {
  color: #fff;
  font-family: inherit;
  font-size: 2em;
  font-weight: bold;
  width: 25px;
  border: none;
  background-color: transparent;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    /* display: none; <- Crashes Chrome on hover */
    -webkit-appearance: none;
    margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type=number] {
    -moz-appearance:textfield; /* Firefox */
}

input[type='text'],
input[type='date'],
input[type='time'],
textarea {
  font-family: inherit;
  background-color: #1f1b24;
  color: #fff;
  border: 1px solid #1f1b24;
  border-radius: 2px;
  display: block;
  font-size: 16px;
  padding: 10px;
  width: 100%;
}

.form-control {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.input-label-group {
  margin: 0px 1px 0px 1px;
  width: 100%;
}

.btn {
  cursor: pointer;
  background-color: #9c88ff;
  box-shadow: var(--box-shadow);
  color: #fff;
  border: 0;
  border-radius: 3px;
  display: block;
  font-size: 16px;
  margin: 10px 0 30px;
  padding: 10px;
  width: 100%;
}

.btn:focus,
.delete-btn:focus {
  outline: 0;
}

.list {
  list-style-type: none;
  padding: 0;
  margin-bottom: 40px;
}

.list li {
  background-color: #fff;
  box-shadow: var(--box-shadow);
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  padding: 10px;
  margin: 10px 0;
}

.list li.plus {
  background-color: #1f1b24;
  border-right: 5px solid #2ecc71;
}

.list li.minus {
  background-color: #1f1b24;
  border-right: 5px solid #c0392b;
}

.delete-btn {
  cursor: pointer;
  background-color: #e74c3c;
  border: 0;
  color: #fff;
  font-size: 20px;
  line-height: 20px;
  padding: 2px 5px;
  position: absolute;
  /* top: -50%; */
  /* left: 20px; */
  transform: translate(-100%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.list li:hover .delete-btn {
  opacity: 1;
}
